import { Component, OnInit } from '@angular/core';
import { TimelineItem } from 'src/app/models/timeline.models';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit {
  educationTimelineItems: TimelineItem[] = [];
  experienceTimelineItems: TimelineItem[] = [];
  skillsTimelineItems: string[][] = [];

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.http.get<TimelineItem[]>('assets/data/educacion.json').subscribe(data => {
      this.educationTimelineItems = data;
    });

    this.http.get<TimelineItem[]>('assets/data/experiencia.json').subscribe(data => {
      this.experienceTimelineItems = data;
    });

    this.http.get<string[][]>('assets/data/habilidades.json').subscribe(data => {
      this.skillsTimelineItems = data;
    });
  }

}
